import { Icon28FavoriteOutline } from "@vkontakte/icons"
import { FixedLayout, Footer, Group, IconButton, Search, SimpleCell } from "@vkontakte/vkui"
import { useEffect, useState } from "react"

const groupsRequest = new XMLHttpRequest()
groupsRequest.open("GET", "https://journal.bsuir.by/api/v1/groups", false)
groupsRequest.send()

const groups = JSON.parse(groupsRequest.responseText)

function GroupList() {

    const [search, setSearch] = useState("")
    const [groupsResult, setGroupsResult] = useState(() => {
        const searchValue = search.toLowerCase();
        return groups.filter(
            ({ name }) => name.toLowerCase().indexOf(searchValue) > -1
        )
    })

    useEffect(() => {
        const searchValue = search.toLowerCase();
        console.log("useEffect")
        setGroupsResult(groups.filter(
            ({ name }) => name.toLowerCase().indexOf(searchValue) > -1
        ))
    }, [search])

    return (
        <span>
            <FixedLayout vertical="top">
                <Search 
                    value={search}
                    onChange={(e) => { setSearch(e.target.value) }}
                    after={null}
                />
            </FixedLayout>
            <Group style={{ paddingTop: 40}}>
                {console.log("return")}
                {groupsResult.length > 0 &&
                    groupsResult.map((group) => (
                        <SimpleCell
                            key={group.id}
                            after={<IconButton><Icon28FavoriteOutline /></IconButton>}
                        >
                            {group.name}
                        </SimpleCell>
                    ))}
                {groupsResult.length === 0 && <Footer>Ничего не найдено</Footer>}
            </Group>
        </span>
    )
}

export default GroupList